.navigation {
	position: fixed;
	z-index: 2;
	width: 100%;
	height: 10vh;

	.nav {
		display: flex;

		.left {
			// border: 1px solid rgb(240, 220, 44);
			height: 80px;
			width: 0%;

		}

		.right {
			height: fit-content;
			width: 100%;
			display: flex;
			justify-content: center;

			&.out {
				justify-content: end;
			}

			.tabs {
				height: 100%;
				display: flex;
				flex-direction: row;
				justify-content: flex-end;
				padding-top: 50px;

				&.out {
					animation: ease;
					flex-direction: column;
					padding-top: 20px;
				}

				:hover {
					.tab_container>.tab>.tab_text_container {
						display: flex !important;
					}

					.tab_item_underline {
						display: block;
					}
				}

				.tab_container {
					display: flex;
					justify-content: center;
					align-content: center;
					flex-direction: column;
					padding-right: 20px;

					&.hide {
						display: none;
					}

					.tab {
						display: flex;
						padding: 5px 10px 5px 10px;
						cursor: pointer;
						justify-content: start;

						.tab_item_container {

							.tab_item {
								display: flex;

								.tab_icon {
									height: 20px;
									width: 20px;
									background-position: center center;
									background-size: contain;
									background-repeat: no-repeat;
								}

								.tab_text_container {
									padding-left: 10px;

									&.out {
										display: none;
									}

									.tab_text {
										color: aliceblue;
									}

									@media only screen and (max-width: 500px) {
										display: none;
									}
								}
							}
						}
					}

					.tab_item_underline {
						width: 0%;
						height: 3px;
						margin-bottom: 5px;
						background-color: aliceblue;
						transition: 0.3s;

						&.active {
							width: 100%;
						}
					}
				}

				.tab_container:hover .tab_item_underline {
					width: 100%;
					transition: 0.3s;
				}
			}
		}
	}
}